.form {
  inline-size: 50%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.input {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  inline-size: 100%;
  background: #ddd;
  color: #000;
  padding: 1%;
  font-size: 1.5em;
  padding: 10px;
  border: 1px solid #000;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.input:hover {
  background: #ccc;
}

.buttons {
  inline-size: 100%;
  display: flex;
  gap: 15px;
}

.button {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  color: #000;
  inline-size: 100%;
  padding: 2.5%;
  font-size: 1.5em;
  background-color: #ddd;
  cursor: pointer;
  border: 1px solid #000;
  border-radius: 3px;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #ccc;
}

.label {
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  color: #000;
  font-size: 1.5em;
  font-weight: 600;
}

.setButton {
  inline-size: 50%;
  padding: 1.25%;
}

@media (width < 600px) {
  .form,
  .setButton {
    inline-size: 100%;
  }
}
